<script>
  import AMapLoader from "../util/AMap";
  import ImgMark from '../assets/10_location.png'

  export default {
    props: {
      option: Object
    },
    methods: {
      __initComponent() {
        AMapLoader().then(AMap => {
          let defaultOption = {
            icon: ImgMark,
            content: `
                    <span class="mark-container">
                        <img src=${ImgMark} alt="">
                    </span>`,
            offset: new AMap.Pixel(-87, -70)
          }
          this.$marker = new AMap.Marker({
            ...this.option,
            ...defaultOption,
          })
          this.$parent.$amap.add(this.$marker);
        })
      },
    },
    render() {
      return null;
    },
    destroyed() {
      this.$marker && this.$parent.$amap.remove(this.$marker);
      this.$marker = null;
    }
  }
</script>

<style lang="scss">
  .mark-container {
    position: relative;
    width: 176px;
    height: 125px;
    display: inline-block;
  }
</style>
