import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

export const routes = [
  {
    path: "/",
    name: "Home",
    meta: {
      title: '首页'
    },
    component: Home,
  },
  {
    path: "/infoArchive",
    name: "infoArchive",
    meta: {
      title: '园区信息档案'
    },
    component:  () => import("../views/InfoArchive.vue"),
  },
  {
    path: "/enQuality",
    name: "enQuality",
    meta: {
      title: '环境质量'
    },
    component: () => import("../views/EnQuality.vue")
  },
  {
    path: "/enSupervision",
    name: "enSupervision",
    meta: {
      title: '环境监督'
    },
    component: () => import("../views/EnSupervision.vue")
  },
  {
    path: "/pdInformation",
    name: "pdInformation",
    meta: {
      title: '污染排放信息'
    },
    component: () => import("../views/PdInformation.vue")
  },
  {
    path: "/video",
    name: "video",
    meta: {
      title: '视频'
    },
    component: () => import("../views/Video.vue")
  },
  {
    path: "/grade",
    name: "grade",
    meta: {
      title: '绩效评估'
    },
    component: () => import("../views/Grade.vue")
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next()
})

export default router;
