                  return colorList[2]
                }
              }
            },
            coordinateSystem: 'polar',
          }]
        },
        right_1_1: {},
        right_2_1: {},
        right_3_1: [],
        polar: {
          title: {
            text: '极坐标双数值轴',
            theme: "dark",
          },
          legend: {
            data: ['line']
          },
          polar: {
            center: ['50%', '50%']
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'cross'
            }
          },
          angleAxis: {
            type: 'value',
            startAngle: 0
          },
          radiusAxis: {
            min: 0
          },
          series: [
            {
              coordinateSystem: 'polar',
              name: 'line',
              type: 'line',
              showSymbol: false,
              data: data
            }
          ],
          animationDuration: 1000
        }
      }
    }
  }
</script>

