
export default {
  randomInt(min,max) {
    return min + parseInt(Math.random()*(max-min + 1))
  },
  radomArrayInt(min,max, length) {
    let res = []
    for (let i = 0; i < length; i++) {
      let tmp = min +  parseInt(Math.random()*(max-min))
      res.push(tmp)
    }
    return res
  },
  radomArrayPoint(min,max, length) {
    let res = []
    for (let i = 0; i < length; i++) {
      let tmp = Number((Math.random() * max).toFixed(3))
      res.push(tmp)
    }
    return res
  },
  pollutantsWithValue(min,max) {
    let pollutants = this.genPollutants()
    let values = this.radomArrayInt(min,max, pollutants.length)
    return pollutants.map((v,i) => ({
      value: values[i],
      name: v
    }))
  },
  genPollutants() {
    var nameList = [
      "二氧化硫","悬浮物","丁苯","锡","臭氧","氮氧化物","硝基苯","乙醇","碘化物"
    ];
    // nameList.length = 6
    return nameList.sort(() => Math.random() - 0.5)
  },
  genCompanys(length) {
    var nameList = [
      '南通瑞赢环保科技有限公司',
      '南通南大华科技环保有限公司',
      '南通圣龙环保科技有限公司',
      '南通四方印染有限公司',
      '南通鸿羊毛制品有限公司',
      '南通市西部水务有限公司',
      '江苏锯末羊毛制品有限公司',
      '南通宏瑞染整有限公司',
    ];
    nameList.sort(() => Math.random() - 0.5)
    return nameList.slice(0,length)
  },
  genTotals(length) {
    var nameList = [
      'SO2',
      'NOx',
      'COD',
      'NH3-N',
      'VOCs',
      '危废',
      '污水',
    ];
    nameList.sort(() => Math.random() - 0.5)
    return nameList.slice(0,length)
  },
  genCrafts(length) {
    var nameList = [
      '氯化工艺',
      '聚合工艺',
      '合成氨工艺',
      '加氢工艺',
      '烷基化工艺',
      '磺化工艺',
      '氧化工艺',
      '氨基化工艺',
    ];
    nameList.sort(() => Math.random() - 0.5)
    return nameList.slice(0,length)
  },
  getMax(arr) {
    let max;
    arr.forEach(v => {
      if(!max || v.value > max) {
        max = v.value
      }
    })
    return max
  }
}