<template>
  <div id="map-container" :class="{'full-screen': full}">
    <div class="error text-danger" v-if="error">
      {{error}}
    </div>
    <slot :error="error"></slot>
  </div>
</template>
<script>
  // import AMapLoader from "@amap/amap-jsapi-loader";
  import AMapLoader from "../util/AMap";
  export default {
    data() {
      return {
        error: "",
        full: false,
        once:true,
        inited:false,
      }
    },
    destroyed() {
    },
    updated() {
      if(this.inited)
      {
        this.init();
      }
    },
    methods: {
      init() {
        for ( let i = 0 ; i < this.$children.length; i++ ) {
          this.$children[i].__initComponent && this.$children[i].__initComponent()
        }
        if(this.once)
        {
          setTimeout(()=>{
            this.$amap && this.$amap.setFitView();
          },500)
          this.once = false;
        }
      }
    },
    mounted() {
      AMapLoader().then((AMap) => {
        setTimeout(() => {
          this.$amap = new AMap.Map('map-container', {
            mapStyle: 'amap://styles/blue',
            resizeEnable: true,
            rotateEnable: true,
            pitchEnable: true,
            zoom: 17,
            pitch: 60,
            rotation: -15,
            viewMode: '3D',//开启3D视图,默认为关闭
            buildingAnimation: true,//楼块出现是否带动画
            expandZoomRange: true,
            zooms: [3, 20],
            center: [121.291057,32.128147]
          });
          this.init()
          this.inited = true
        },1000)
        // this.$infoWindow = new AMap.InfoWindow({offset: new AMap.Pixel(0, -70)});
      }).catch(e => {
        if (e) {
          this.error = e;
        }
      })
    },
  }
</script>
<style lang="scss" scoped>
  #map-container {
    width: 100%;
    height: 100%;
    transition: all 1ms;
    position: relative;
    .error {
      width: 100%;
      height: 100%;
      font-size: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    
    .map-toolbar {
      position: absolute;
      right: 0;
      bottom: 0;
      z-index: 999;
      display: inline-flex;
    }
  }
  .full-screen {
    position: fixed!important;
    z-index: 999;
    left: 0;
    top: 0;
  }
</style>
