<template>
  <div id="nav">
    <div class="logo">工业园区生态环境管理平台</div>
    <div class="nav-link">
      <router-link v-for="r in routes" :to="r.path" :key="r.name">
        {{r.meta.title || r.name}}
      </router-link>
    </div>
    <div class="right-info">
      <div v-if="info.key" class="cycle">
        {{info.key}}:<span>{{info.value}}</span>
      </div>
      <v-divider inset vertical></v-divider>
      <div class="week">
        {{weekDay}}
        <br>
        {{date}}
      </div>
      <v-divider inset vertical></v-divider>
      <div class="userInfo">
        用户
        <i class="logout"></i>
      </div>
    </div>
  </div>
</template>

<script>
  import {routes} from "../router"
  import moment from 'moment'

  moment.locale("zh-cn");
  export default {
    data() {
      return {
        routes: routes,
        Date: new Date(),
        info: {},
        infos: [
          {
            key: "AQI",
            value: "75",
          },
          {
            key: "空气监测",
            value: "优秀",
          },
          {
            key: "水质",
            value: "I类",
          },
        ]
      };
    },
    mounted() {
      setInterval(() => this.Date = new Date(), 1000)
      let infoIndex = 1;
      this.info = this.infos[infoIndex]
      setInterval(() => {
        this.info = this.infos[infoIndex]
        infoIndex++
        if (infoIndex === this.infos.length) infoIndex = 0
      }, 3000)
    },
    computed: {
      // info: function() {
      //   return this.infos[moment(this.Date).unix() % this.infos.length]
      // },
      date: function () {
        return moment(this.Date).format("YYYY:MM:DD")
      },
      weekDay: function () {
        return moment(this.Date).format("dddd")
      },
    }
  }
</script>

