<template>
  <div>
  </div>
</template>

<script>
  import AMapLoader from "../util/AMap";
  import ImgAir from '../assets/28_sign_air.png'
  import ImgEnterprise from '../assets/29_sign_enterprise.png'
  import ImgPM from '../assets/30_sign_PM.png'
  import ImgRain from '../assets/31_sign_rain.png'
  import ImgStench from '../assets/32_sign_stench.png'
  import ImgVideo from '../assets/33_sign_video.png'
  import ImgVOC from '../assets/34_sign_VOC.png'
  import ImgWasteGas from '../assets/35_sign_waste_gas.png'
  import ImgWasteWater from '../assets/36_sign_waste_water.png'
  import ImgWater from '../assets/37_sign_water.png'
  import Mock from "mockjs";
  
  export default {
    components:{
    },
    props: {
      option: Object
    },
    mounted() {
      let self = this;
      let resizeTimeout;
      window.onresize = function() {
        clearTimeout(resizeTimeout);
        resizeTimeout = setTimeout(function () {
          self.layout && self.layout();
        }, 20);
      };
    },
    data() {
      return{
        onlineMonitor: false,
        file: false,
        showVideo: false,
        infos: [...new Array(4).keys()].map(v => ({
          year: 2017+v,
          info: Mock.Random.cword(20, 40)
        })).reverse()
      }
    },
    methods: {
      __initComponent() {
        console.log("init marker")
        AMapLoader().then(AMap => {
          this.$marker && this.$parent.$amap.remove(this.$marker);
          this.$marker = new AMap.Marker({
            ...this.option,
            content: `
                    <span class="maker-container">
                        <img src=${this.__getMakerImg(this.option.type)} alt="">
                    </span>`,
            offset: new AMap.Pixel(-23, -65)
          })
          this.$marker.on('click', this.showWindow);
          this.$parent.$amap && this.$parent.$amap.add(this.$marker);
        })
      },
      showWindow(e) {
        AMapLoader().then(AMap => {
          // infowidnow 的 innerHTML
          var infoWindowContent = `
            <div class="custom-window">
              <p>${this.option.company}</p>
              <div class="custom-content">
                <div class="list">
                 法人: ${this.option.person}
                </div>
                <div class="list">
                   污染源类型: ${this.option.pollutionType}
                </div>
                <div class="list">
                   污染源名称: ${this.option.pollutionName}
                </div>
                <div class="list">
                   污染源编码: ${this.option.pollutionCode}
                </div>
                <div class="list">
                   污染源地址: ${this.option.pollutionAddress}
                </div>
                <div class="buttons">
                  <a onclick="showMonitor(${this.option.Id})"> 企业发展历程</a>
                  <a  onclick="showFile(${this.option.Id})"> 一企一档</a>
                  ${this.option.type==="video" ?  "<a onclick=\"showVideo()\">在线监控</a>" :""}
                </div>
              </div>
            </div>
          `
          // 创建一个自定义内容的 infowindow 实例
          var infoWindow = new AMap.InfoWindow({
            position: e.target.getPosition(),
            offset: new AMap.Pixel(0, -60),
            content: infoWindowContent
          });
          infoWindow.open(this.$parent.$amap)

          // this.$parent.$infoWindow.setContent("我的位置是" + this.option.position.join(","));
          // this.$parent.$infoWindow.open(this.$parent.$amap, e.target.getPosition());
        })

      },
      __getMakerImg(v) {
        switch (v) {
          case "air":
            return ImgAir;
          case "enterprise":
            return ImgEnterprise;
          case "pm":
            return ImgPM;
          case "rain":
            return ImgRain;
          case "stench":
            return ImgStench;
          case "video":
            return ImgVideo;
          case "voc":
            return ImgVOC;
          case "waste_gas":
            return ImgWasteGas;
          case "waste_water":
            return ImgWasteWater;
          case "water":
            return ImgWater;
          default:
            return ImgWater;
        }
      },
      __getRisk(type) {
        switch (type) {
          case "l":
            return "低";
          case "m":
            return "中";
          case "h":
            return "高";
          default:
            return "低";
        }
      },
      __getRiskClass(type) {
        switch (type) {
          case "l":
            return "risk-low";
          case "m":
            return "risk-mid";
          case "h":
            return "risk-high";
          default:
            return "risk-low";
        }
      },
    },
    render() {
      return null;
    },
    destroyed() {
      this.$marker && this.$parent.$amap.remove(this.$marker);
      this.$marker = null;
    }
  }
</script>

