        //     pollutionAddress: Mock.mock('@county'),
        //   })),
        mark: [
          // {
          //   position: [116.333926, 39.997245],
          // }
        ],
      }
    },
    methods: {
      selectChange: function (value) {
        let index = this.selected.indexOf(value)
        if (index > -1) {
          this.selected.splice(index, 1)
        } else {
          this.selected.push(value)
        }
      },
      selectAll: function () {
        if (this.selected.length === this.options.length) {
          this.selected = []
        } else {
          this.selected = this.options.map(v => v.key)
        }
      },
      search: function () {

      },
    }
