<template>
  <div>
    <div class="parent-element-center" v-on:mouseenter="show=true" v-on:mouseleave="show=false">
      <div class="element-center" :style="textStyle">{{ text }}</div>
      <svg :width="diameterShow" :height="diameterShow" :viewbox="viewbox">
        <circle :cx="size" :cy="size" :r="raduisActual" :stroke-width="strokeWidth" stroke="transparent" fill="none"></circle>
        <circle :cx="size" :cy="size" :r="raduisActual" :stroke-width="1" stroke="rgba(26, 241, 169, 0.2)" fill="none"></circle>
        <circle v-if="rate" :text="text" :cx="size" :cy="size" :r="raduisActual" :stroke-width="strokeWidth" :stroke="strokeColor" fill="none" :transform="transform" :stroke-dasharray="strokeDasharray"></circle>
      </svg>
      <div v-if="show" class="hover-info">
        <div v-for="v in details" :key="v">
          {{v}}
        </div>
      </div>
    </div>
    <p>
      {{type}}
    </p>
  </div>
</template>
<script>
  export default {
    props: {
      // 圆环外圈的直径
      size: {
        default: 35
      },
      type: {
        default: ""
      },
      details: {
        default: ""
      },
      // 圆环的小宽度
      strokeWidth: {
        default: 4
      },
      // 圆环的颜色
      strokeColor: {
        default: '#1af1a9'
      },
      // 圆环显示的百分比 这边是小数
      rate: {
        default: 0.8
      },
      // 圆环里面的文字 这里的文字如果跟rate息息相关 可以放到computed计算
      text: {
        default: 10
      },
      // 圆环里面的文字的fontSize大小
      textSize: {
        default: 24
      }
    },
    data() {
      return {
        show: false
      }
    },
    computed: {
      raduisActual () {
        return this.size - this.strokeWidth
      },
      diameterShow () {
        return 2 * this.size
      },
      viewbox () {
        return `0 0 ${this.diameterShow} ${this.diameterShow}`
      },
      strokeDasharray () {
        const perimeter = Math.PI * 2 * this.raduisActual
        const showLength = this.rate * perimeter - 3
        return `${showLength} 1000`
      },
      transform () {
        return `matrix(0,-1,1,0,0,${this.diameterShow})`
      },
      textStyle () {
        let res = {}
        res.fontSize = `${this.textSize}px`
        return res
      }
    }
  }
</script>
