<template>
  <div>
    <v-dialog
      v-model="onlineMonitor"
      width="500"
    >
      <v-card>
        <v-card-title class="headline lighten-2">
          企业发展历程
        </v-card-title>
        <v-card-text>
          <div class="company_info">
            <div v-for="(item,i) in infos" :key="i">
              <div>
                {{item.year}}
              </div>
              <div>
                {{item.info}}
              </div>
            </div>
          </div>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="showVideo"
    >
      <v-card>
        <v-card-title class="headline lighten-2">
          在线监控
          <v-btn
            class="close-btn"
            icon
            dark
            @click="showVideo = false"
          >
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-card-title>
        <v-card-text>
          <div v-if="showVideo" id="videoLayout">
            <video
              v-for="(v) in videoList"
              :key="v"
              controls
              autoplay
              loop
              src="../assets/movie.ogg"></video>
          </div>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="file"
      width="500"
    >
      <v-card>
        <v-card-title class="headline lighten-2">
          一企一档
        </v-card-title>
        <v-card-text>
          {{record}}
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
  // import Mock from "mockjs";
  import $ from 'jquery'
  import Fetch from "../util/Fetch";
  import http from "../util/http";

  const initLayoutContainer = require('opentok-layout-js')
  window.$ = $
  const options = {
    maxRatio: 3 / 2,             // The narrowest ratio that will be used (default 2x3)
    minRatio: 9 / 16,            // The widest ratio that will be used (default 16x9)
    fixedRatio: false,         // If this is true then the aspect ratio of the video is maintained and minRatio and maxRatio are ignored (default false)
    alignItems: 'center',      // Can be 'start', 'center' or 'end'. Determines where to place items when on a row or column that is not full
    bigClass: "OT_big",        // The class to add to elements that should be sized bigger
    bigPercentage: 0.8,        // The maximum percentage of space the big ones should take up
    bigFixedRatio: false,      // fixedRatio for the big ones
    bigAlignItems: 'center',   // How to align the big items
    smallAlignItems: 'center', // How to align the small row or column of items if there is a big one
    maxWidth: Infinity,        // The maximum width of the elements
    maxHeight: Infinity,       // The maximum height of the elements
    smallMaxWidth: Infinity,   // The maximum width of the small elements
    smallMaxHeight: Infinity,  // The maximum height of the small elements
    bigMaxWidth: Infinity,     // The maximum width of the big elements
    bigMaxHeight: Infinity,    // The maximum height of the big elements
    bigMaxRatio: 3 / 2,          // The narrowest ratio to use for the big elements (default 2x3)
    bigMinRatio: 9 / 16,         // The widest ratio to use for the big elements (default 16x9)
    bigFirst: true,            // Whether to place the big one in the top left (true) or bottom right (false).
                               // You can also pass 'column' or 'row' to change whether big is first when you are in a row (bottom) or a column (right) layout
    animate: true,              // Whether you want to animate the transitions
    window: window,            // Lets you pass in your own window object which should be the same window that the element is in
    ignoreClass: 'OT_ignore',  // Elements with this class will be ignored and not positioned. This lets you do things like picture-in-picture
  };
  export default {
    components: {},
    props: {
      option: Object
    },
    mounted() {
      let self = this;
      let resizeTimeout;
      window.onresize = function () {
        clearTimeout(resizeTimeout);
        resizeTimeout = setTimeout(function () {
          self.layout && self.layout.layout();
        }, 20);
      };
      window.showMonitor = async (id) => {
        self.onlineMonitor = !self.onlineMonitor
        let res = await Fetch(http.GetHistory,{Id:id || "1"});
        self.infos = res.data.HistoryList.map(v => ({
          year: v.Date.slice(0,4),
          info: v.Cotent
        }))
        
      }
      window.showFile = async(id) => {
        self.file = !self.file
        let res = await Fetch(http.GetInfo,{Id:id || "1"});
        self.record = res.data.RegTypeName || "我的档案"
      }
      window.showVideo = () => {
        self.showVideo = !self.showVideo
        setTimeout(() => {
          self.layout = initLayoutContainer(document.getElementById("videoLayout"), options);
          self.layout.layout();
        }, 500)
      }
    },
    data() {
      return {
        videoList: [],
        onlineMonitor: false,
        file: false,
        showVideo: false,
        infos: [],
        record:"",
      }
    },
    methods: {},
    render() {
      return null;
    },
    destroyed() {
      this.$marker && this.$parent.$amap.remove(this.$marker);
      this.$marker = null;
    }
  }
</script>

